import * as React from "react";
import { observer } from "mobx-react";
import { LMR, VPage, Image } from "tonva";
import { CMe } from "./CMe";
import { observable } from "mobx";

/* eslint-disable */

export class VMeAssist extends VPage<CMe> {
    private inviteCode: any;
    @observable showTips: any = "none";

    async open() {
        this.openPage(this.page);
    }

    render(member: any): JSX.Element {
        let { inviteCode } = this.controller;
        this.inviteCode = inviteCode.substr(1, 9);
        this.controller.cApp.cBalance.getComputeAchievement();
        return <this.page />;
    }

    private meInfo = observer(() => {
        let { user, showMeDetail } = this.controller;
        if (user === undefined) return null;
        let { name, nick, icon } = user;

        let left = <div onClick={showMeDetail}>
            <Image className="w-3c h-3c mr-3" src={icon} />{" "}
        </div>;
        let contener = <div>
            <div>{nick ? <b>{nick}</b> : <b>{name}</b>}</div>
            <div className="small">{name}</div>
        </div>;

        return <div className="px-4 py-3 cursor-pointer"
            style={{ backgroundColor: "#f9f9f9", width: "92%", borderRadius: "4px", margin: "-2rem auto 0 auto", boxShadow: "0 0 3px #dbdbdb" }}>
            <LMR left={left} > {contener}</LMR>
        </div>

    });

    private myservice = () => {
        let { cSalesTask, cCoupon } = this.controller.cApp;
        let { showMyCustomer } = this.controller;
        let { showMyTasksCompleted } = cSalesTask;
        let onShowMyTasksCompleted = async () => await showMyTasksCompleted();

        let vcoupon = <div className="col text-center" onClick={() => cCoupon.showCreateCoupon({ type: "coupon", product: undefined })} >
            <div>
                <i className="iconfont icon-youhuiquan" style={{ fontSize: "28px", color: "#f6ad15" }} ></i>
            </div>
            <small>
                <small className="text-muted">优惠券</small>
            </small>
        </div>;

        let vassist = <div className="col text-center" onClick={() => cCoupon.showCouponList("credits")} >
            <div>
                <i className="iconfont icon-youhuiquantuangou" style={{ fontSize: "28px", color: "#f6ad15" }} ></i>
            </div>
            <small>
                <small className="text-muted">积分券</small>
            </small>
        </div>;

        let histtask = <div className="col text-center" onClick={onShowMyTasksCompleted}  >
            <div>
                <i className="iconfont icon-renwuwancheng" style={{ fontSize: "28px", color: "#2aa515" }}></i>
            </div>
            <small>
                <small className="text-muted">历史任务</small>
            </small>
        </div>;

        let active = <div className="col text-center" onClick={() => showMyCustomer(2)}>
            <div>
                <i className="iconfont icon-touxiangxiaoren" style={{ fontSize: "28px", color: "#13227a" }}></i>
            </div>
            <small>
                <small className="text-muted">活跃客户</small>
            </small>
        </div >;

        return <div className="row p-4 cursor-pointer">
            {vcoupon}
            {vassist}
            {histtask}
            {active}
        </div>;
    };

    private myacheviment = () => {
        return <div>
            <div className="text-left mx-4 d-flex justify-content-between" >
                <strong>我的业绩</strong>
                <span className="text-muted"> <small></small></span>
            </div>
            <div className="text-center">
                <div className="h1 strong" style={{ color: "#e0620d" }} ><strong>16000</strong></div>
                <div className="small text-muted">今日销量（元）</div>
            </div>
            <div className="m-3 d-flex justify-content-between text-center">
                <div className="py-2" style={{ width: "32%", backgroundColor: "#dbdbdb", borderRadius: "4px" }}>
                    <div className="small text-muted">本月销量</div>
                    <div className="h3"> <strong>60</strong> <span className="h6"><small className="text-muted">万</small></span></div>
                </div>
                <div className="py-2" style={{ width: "32%", backgroundColor: "#dbdbdb", borderRadius: "4px" }}>
                    <div className="small text-muted">年度销量</div>
                    <div className="h3"> <strong>600</strong> <span className="h6"><small className="text-muted">万</small></span></div>
                </div>
                <div className="py-2" style={{ width: "32%", backgroundColor: "#dbdbdb", borderRadius: "4px" }}>
                    <div className="small text-muted">年度奖金</div>
                    <div className="h3"> <strong>6000</strong> <span className="h6"><small className="text-muted">元</small></span></div>
                </div>
            </div>
        </div>
    };

    private starlist = () => {
        return <div>
            <div className="text-left mx-4 d-flex justify-content-between" >
                <strong>明星榜</strong>
                <span className="text-muted"> <small></small></span>
            </div>
            <div className="row d-flex justify-content-between" >
                <div className="col-6 d-flex text-center">
                    <div className="iconfont icon-socar_first_icon pl-3 pr-2 mt-3" style={{ fontSize: "3rem", color: "#e0620d" }} > </div>
                    <div style={{ color: "#e0620d" }} >
                        <div className="h3 mt-3"><strong>冠军</strong></div>
                        <div><strong>张某某</strong></div>
                    </div>
                </div>
                <div className="col-6 mt-3 small text-muted text-left">
                    <div className="py-1 strong">
                        <strong>
                            <span className="text-warning h4 px-2" ><strong>2</strong></span>
                            <span className="mb-3">科研部 李某某</span>
                        </strong>
                    </div>
                    <div className="py-1">
                        <strong>
                            <span className="text-warning h4 px-2"><strong>3</strong></span>
                            <span className="mb-3">科研部 陈某某</span>
                        </strong>
                    </div>
                </div>
            </div >
        </div >
    };

    private title = () => {
        let { showSet } = this.controller;
        return <div className="bg-primary text-right" style={{ height: "5rem" }}>
            <div className="pt-2" onClick={showSet}>
                <i className="iconfont icon-shezhi1" style={{ fontSize: "15px", color: "#ffffff" }}></i>
            </div>
        </div>;
    };

    private page = observer(() => {

        return <div style={{ backgroundColor: "#f9f9f9" }}>
            <this.title />
            <this.meInfo />
            <this.myservice />
            <this.myacheviment />
            <this.starlist />
        </div>

    });
}
